@media (max-width: 790px){
    .row_space::after{
        display: none !important;
    }
    .row_space::before{
        display: none !important;
    }
    .home_each{
        width: 30% !important;
    }
}
.submit_button{
    background-color: #E60000;
    border: none;
}
.submit_button:hover{
    background-color: #ca0101;
}

.form_login{
    margin: 50px 0px;
}

.form_space{
    width: 80%;
    max-width: 1500px;
    margin: 0 auto;

}
.home_form{

}

.row_space{
    width: 100%;
    display: flex;
    justify-content: space-around;
}

.row_space::before{
    content: "";
    border-bottom: 2px solid #d5d7db;
    width: 15%;
}

.row_space::after{
    content: "";
    border-bottom: 2px solid #d5d7db;
    width: 15%;
}